import storage from '../../static/lib/storage'

export default {
  props: {
    isShow: {
      type: Boolean,
      default () {
        return false
      }
    }
  },
  data () {
    return {
      slogan: this.$configs.CONST.SLOGAN,
      keywords: '',
      hotSearchList: [],
      searchList: [],
      isFocus: false,
      isSearch: false,
      statusMapper: {
        product: 'haveProductReviews',
        category: 'haveCategoryInformation',
        fuzzy: 'fuzzyMatching',
        notFound: 'allWithout'
      },
      timer: null,
      historySearch: []
    }
  },
  methods: {
    handleBannerPopShow () {
      this.$emit('handleBannerPopShow')
    },
    async onSearch () {
      if (!this.keywords.trim()) {
        return false
      } else {
        let res = await this.$requests.indexKeysSearch({
          KEY_INFO: this.keywords.trim()
        })
        if (res.status === 200 && res.data.result === 'success') {
          switch (res.data.searchStatus) {
            case this.statusMapper.category:
              this.$router.push({
                name: 'product-id',
                params: {
                  id: res.data.CATEGORY_ID
                },
                query: {
                  category_name: this.keywords.trim()
                }
              })
              break
            case this.statusMapper.product:
              this.$router.push({
                name: 'productDetail-id',
                params: {
                  id: res.data.PRODUCT_ID
                }
              })
              break
            case this.statusMapper.fuzzy:
              this.$router.push({
                name: 'result-status',
                params: {
                  status: 'fuzzy'
                },
                query: {
                  keywords: this.keywords.trim()
                }
              })
              break
            case this.statusMapper.notFound:
              this.$router.push({
                name: 'result-status',
                params: {
                  status: 'notFound'
                },
                query: {
                  keywords: this.keywords.trim()
                }
              })
              break
          }
        }
      }
    },
    async onChange () {
      if (this.keywords.trim() === '') {
        this.isFocus = true
        this.isSearch = false
      } else {
        this.searchList = await this.searchAutoComplete()
        if (this.searchList) {
          this.isFocus = false
          this.isSearch = true
        } else {
          this.isFocus = false
          this.isSearch = false
        }
      }
    },
    async onFocus () {
      if (this.keywords.trim() === '') {
        this.getHistorySearch()
        this.isFocus = true
        const data = {
          CUR_PAGE: 1
        }
        const res = await this.$requests.indexSearchHot(data)
        if (res.status === 200 && res.data.result === 'success') {
          this.hotSearchList = res.data.hotsearchlogObj
        }
      }
    },
    onBlur () {
      this.timer = setTimeout(() => {
        this.isFocus = false
      }, 300)
    },
    async searchAutoComplete () {
      let resList = []
      const data = {
        KEY_INFO: this.keywords.trim()
      }
      const res = await this.$requests.indexSearchAutoComplete(data)
      if (res.status === 200 && res.data.result === 'success') {
        resList = res.data.productList
      }
      return resList
    },
    async handleKeyWordTagClick (obj) {
      // 历史搜索本地存储
      let historySearch = storage.get('set_history_search')
      if (historySearch) {
        let index = historySearch.findIndex((item) => {
          return item.ID === obj.ID
        })
        if (index !== -1) {
          historySearch.splice(index, 1)
        }
        historySearch.unshift(obj)
        storage.set('set_history_search', historySearch)
      } else {
        let tmpArr = []
        tmpArr.unshift(obj)
        storage.set('set_history_search', tmpArr)
      }

      clearTimeout(this.timer)
      let res = await this.$requests.indexKeysSearch({
        KEY_INFO: obj.names.trim()
      })
      if (res.status === 200 && res.data.result === 'success') {
        switch (res.data.searchStatus) {
          case this.statusMapper.category:
            this.$router.push({
              name: 'company-id',
              params: {
                id: res.data.CATEGORY_ID
              },
              query: {
                category_name: obj.value.trim()
              }
            })
            break
          case this.statusMapper.product:
            this.$router.push({
              name: 'productDetail-id',
              params: {
                id: res.data.PRODUCT_ID
              }
            })
            break
          case this.statusMapper.fuzzy:
            this.$router.push({
              name: 'result-status',
              params: {
                status: 'fuzzy'
              },
              query: {
                keywords: obj.names.trim()
              }
            })
            break
          case this.statusMapper.notFound:
            this.$router.push({
              name: 'result-status',
              params: {
                status: 'notFound'
              },
              query: {
                keywords: obj.names.trim()
              }
            })
            break
        }
      }
    },
    handleKeyWordTagClick2 (obj) {
      // 历史搜索本地存储
      let historySearch = storage.get('set_history_search')
      if (historySearch) {
        let index = historySearch.findIndex((item) => {
          return item.ID === obj.ID
        })
        if (index !== -1) {
          historySearch.splice(index, 1)
        }
        historySearch.unshift(obj)
        storage.set('set_history_search', historySearch)
      } else {
        let tmpArr = []
        tmpArr.unshift(obj)
        storage.set('set_history_search', tmpArr)
      }
      if (obj.TYPE === '1') {
        this.$router.push({
          name: 'productDetail-id',
          params: { id: obj.ID }
        })
      } else {
        this.$router.push({
          name: 'company-id',
          params: { id: obj.ID },
          query: { category_name: obj.NAME }
        })
      }
    },
    // 本地存储的历史搜索
    getHistorySearch () {
      this.historySearch = storage.get('set_history_search')
      console.log(this.historySearch)
    },
    // 删除本地存储的历史搜索
    deleteHistorySearch () {
      storage.remove('set_history_search')
      this.historySearch = []
    }
  }
}
